}

.brands {
  position: relative;
}

.brands:before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 1;
  background: radial-gradient(circle, rgba(0, 0, 0, 0) 0%, #fff 80%);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

